import React, { useState } from 'react';
import { Form, Input, Button } from 'antd';
import { LockOutlined, UserOutlined, MailOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

import Modal from '../modal/Modal';
import FormTitle from '../formTitle/FormTitle';
import ConfirmEmail from './ConfirmEmail';
import style from './RegistrationForm.module.scss'
import validate from '../../../utils/validate';

function RegistrationForm(props) {
    const {
        values,
        touched,
        errors,
        isSubmitting,
        handleChange,
        handleBlur,
        handleSubmit,
        handleReset,
        dirty,
    } = props;

    const [isSucces, setSucces] = useState(false)
    const onFinish = values => {

        handleSubmit(values)
    };

    return (
        <>
            <FormTitle
                title='–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è'
                descr=' –î–ª—è –≤—Ö–æ–¥–∞ –≤ —á–∞—Ç, –≤–∞–º –Ω—É–∂–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è'
            />

            <Modal>
                {!isSucces
                    ? <>
                        <Form name="nest-messages" onFinish={onFinish}>

                            <Form.Item hasFeedback
                                validateStatus={validate('email', errors, touched)}
                                help={touched.email && errors.email}
                            >
                                <Input
                                    id='email'
                                    placeholder="E-Mail"
                                    size='large'
                                    prefix={<MailOutlined />}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('name', errors, touched)}
                                help={touched.name && errors.name}
                            >
                                <Input
                                    id='name'
                                    placeholder="–í–∞—à–µ –∏–º—è"
                                    size='large'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<UserOutlined />}
                                />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('password', errors, touched)}
                                help={touched.password && errors.password}
                            >
                                <Input.Password size='large'
                                    id='password'
                                    placeholder="–ü–∞—Ä–æ–ª—å"

                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<LockOutlined />} />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('confirm', errors, touched)}
                                help={touched.confirm && errors.confirm}
                            >
                                <Input.Password size='large'
                                    id='confirm'
                                    placeholder="–ø–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å"

                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<LockOutlined />} />
                            </Form.Item>


                            <Form.Item >
                                <Button type="primary"
                                    htmlType="submit"
                                    className={style.register__btn}
                                >
                                    –ó–ê–†–ï–ì–ò–°–¢–†–ò–†–û–í–ê–¢–¨–°–Ø
                                </Button>
                            </Form.Item>

                        </Form>
                        <Link to='auth'>–í–æ–π—Ç–∏ –≤ –∞–∫–∫–∞—É–Ω—Ç</Link>

                    </>
                    : <ConfirmEmail />
                }

            </Modal>
        </>
    )

}







export default RegistrationForm;
