

import React from 'react';
import formatDistanceToNow from 'date-fns/formatDistanceToNow'
import ruLocale from 'date-fns/locale/ru'

import style from './DialogItem.module.scss'
import ReadChecker from '../../../readCheker/ReadCheker'
import getMessageTime from '../../../../utils/getMessageTime';


function DialogItem({ user, newMessage, unReaded }) {

    return (
        <div className={style.dialog__item}>

            <div className={style.dialog__item_avatar + ' ' + `${user.online && style.online}`}></div>
            <div className={style.dialog__item_info}>
                <div>
                    <div className={style.dialog__item_title}>
                        {user.fullName}
                    </div>

                    <div className={style.dialog__item_lastMessage}>
                        {newMessage.text}
                    </div>
                </div>
                <div>
                    <div className={style.dialog__item_date}>

                        {getMessageTime(newMessage.date)}
                    </div>

                    {newMessage && unReaded > 0
                        ? <div className={style.dialog__item_messageCount}>
                            {unReaded > 9 ? '+9' : unReaded}
                        </div>
                        : <div className={style.checked}>
                            <ReadChecker isMe={newMessage.isMe} isReaded={newMessage.isReaded} />
                        </div>}

                </div>
            </div>
        </div>
    )
}

export default DialogItem;
