
import React from 'react';

import checkedSvg from '../../assets/img/checked.svg'
import noCheckedSvg from '../../assets/img/noChecked.svg'
import style from './ReadCheker.module.scss'

function ReadCheker({ isMe, isReaded }) {
    return (
        <>
            {isMe ? isReaded
                ? <img className={style.message__checked} src={checkedSvg} />
                : <img className={style.message__checked} src={noCheckedSvg} />
                : null
            }
        </>
    )
}

export default ReadCheker;
