
import React, { useState } from 'react';

import Messages from '../messages/Messages'
import MessagesInput from '../messagesInput/MessagesInput';
import style from './ChatContent.module.scss'

function ChatContent() {

    const staticMessages = [
        {
            user: { lastName: 'test', avatar: null },
            text: '–°–∞–ª–∞–º, –ë—Ä—É—Ç! –ß—ë, –∫–∞–∫, —É–Ω–∏—á—Ç–æ–∂–∏–ª —Ñ–ª–æ—Ç –≥–∞–ª–ª–æ–≤? üñêüèª',
            date: new Date(),
            isMe: true,
            isReaded: true,
            attachments: [
                {
                    filename: 'image.jpg',
                    url: 'https://source.unsplash.com/50x50/?nature,water'
                },

                {
                    filename: 'image.jpg',
                    url: 'https://source.unsplash.com/50x50/?nature,water'
                },

                {
                    filename: 'image.jpg',
                    url: 'https://source.unsplash.com/50x50/?nature,water'
                },
            ]
        },
        {
            user: { lastName: 'test', avatar: null },
            text: '–°–∞–ª–∞–º, –ë—Ä—É—Ç! –ß—ë, –∫–∞–∫, —É–Ω–∏—á—Ç–æ–∂–∏–ª —Ñ–ª–æ—Ç –≥–∞–ª–ª–æ–≤? üñêüèª',
            date: new Date(),
            isMe: false,
        },
        {
            user: { lastName: 'test', avatar: null },
            isMe: false,
            isTyping: true
        },
    ]

    const [messages, setMessages] = useState(staticMessages)

    const sendMessages = (text) => {
        const obj = {
            user: { lastName: 'test', avatar: null },
            text,
            date: new Date(),
            isMe: true
        }
        setMessages([
            ...messages,
            obj
        ])
    }

    return (
        <div className={style.content}>
            <div className={style.messages}>
                {messages.map(item => <Messages
                    text={item.text}
                    user={item.user}
                    date={item.date}
                    isMe={item.isMe}
                    isReaded={item.isReaded}
                    isTyping={item.isTyping}
                    attachments={item.attachments}
                    key={item.date}
                />
                )}
            </div>

            <MessagesInput sendMessages={sendMessages} />
        </div>
    )
}

export default ChatContent;
