
import React from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, Button } from 'antd';
import { LockOutlined, UserOutlined } from '@ant-design/icons';

import Modal from '../modal/Modal';
import FormTitle from '../formTitle/FormTitle';
import validate from '../../../utils/validate';

function AuthForm(props) {
    const {
        values,
        touched,
        errors,
        isSubmitting,
        handleChange,
        handleBlur,
        handleSubmit,
        handleReset,
        dirty,
    } = props;


    const onFinish = values => {

        handleSubmit()
    }

    return (
        <>
            <FormTitle
                title='–í–æ–π—Ç–∏ –≤ –∞–∫–∫–∞—É–Ω—Ç'
                descr='–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–æ–π–¥–∏—Ç–µ –≤ —Å–≤–æ–π –∞–∫–∫–∞—É–Ω—Ç'
            />

            <Modal>

                <Form
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >


                    <Form.Item hasFeedback
                        validateStatus={validate('name', errors, touched)}
                        help={errors.name}
                    >
                        <Input
                            id='name'
                            placeholder="Username"
                            size='large'
                            prefix={<UserOutlined />}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        />
                    </Form.Item>

                    <Form.Item hasFeedback
                        validateStatus={validate('password', errors, touched)}
                        help={touched.password && errors.password}
                    >

                        <Input.Password size='large'
                            id='password'
                            placeholder="–ü–∞—Ä–æ–ª—å"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            prefix={<LockOutlined />}
                        />

                    </Form.Item>


                    <Form.Item>
                        <Button
                            style={{ width: '100%', height: '56px', fontSize: '16px', fontWeight: 500 }} type="primary" htmlType="submit"
                        >

                            –í–û–ô–¢–ò –í –ê–ö–ö–ê–£–ù–¢

                </Button>
                    </Form.Item>

                </Form>
                <Link to='registration'> –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è </Link>
            </Modal>
        </>
    )
}

export default AuthForm;
