

import { withFormik } from 'formik';

import AuthForm from './AuthForm'

const AuthFormContainer = withFormik({

    mapPropsToValues: () => ({
        name: '',
        password: '',
    }),

    validate: values => {
        let errors = {};
        if (!values.name) {
            errors.name = '–í–≤–µ–¥–∏—Ç–µ –∏–º—è';
        }
        if (!values.password) {
            errors.password = '–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
        }
        return errors;
    },

    handleSubmit: (values, { setSubmitting }) => {
        setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            setSubmitting(false);
        }, 1000);
    },

    displayName: 'AuthForm', // helps with React DevTools
})(AuthForm);



export default AuthFormContainer;
