

import { withFormik } from 'formik';

import RegistrationForm from './RegistrationForm'

const RegistrationFormContainer = withFormik({

    mapPropsToValues: () => ({
        email: '',
        name: '',
        password: '',
        confirm: ''
    }),


    validate: values => {
        let errors = {};



        if (!values.email) {
            errors.email = '–í–≤–µ–¥–∏—Ç–µ email';
        } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(
                values.email
            )
        ) {
            errors.email = '–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω—ã–π email';
        }
        if (!values.name) {
            errors.name = '–í–≤–µ–¥–∏—Ç–µ –∏–º—è';
        }
        if (!values.password) {
            errors.password = '–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
        }
        if (!values.confirm) {
            errors.confirm = '–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
        } else if (values.password !== values.confirm) {
            errors.confirm = '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç';
        }
        return errors;
    },

    handleSubmit: (values, { setSubmitting }) => {
        setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            setSubmitting(false);
        }, 1000);
    },

    displayName: 'RegistationForm', // helps with React DevTools
})(RegistrationForm);



export default RegistrationFormContainer;
